/* ==== global declaration ==== */

@font-face {
    
    font-family: "Open Sans";;
    src: url("/assets/webfonts/OpenSans.woff2") format("woff2");
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: "i am a player";
    src: url("/assets/webfonts/i_am_a_player-webfont.woff2") format("woff2");
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: "Tungsten Bold";
    src: url("/assets/webfonts/Tungsten-Bold.woff2") format("woff2");
    font-weight: bold;
    font-style: normal;
}
@font-face {
    font-family: "Tungsten Book";
    src: url("/assets/webfonts/Tungsten-Book.woff2") format("woff2");
    font-style: normal;
}
@font-face {
    font-family: "Tungsten Medium";
    src: url("/assets/webfonts/Tungsten-Medium.woff2") format("woff2");
    font-style: normal;
}
@font-face {
    font-family: "Tungsten Semibold";
    src: url("/assets/webfonts/Tungsten-SemiBold.woff2") format("woff2");
    font-style: normal;
}
@font-face {
    font-family: "Tungsten light";
    src: url("/assets/webfonts/Tungsten-Light.woff2") format("woff2");
    font-style: normal;
}
@font-face {
    font-family: "Tungsten xtra-light";
    src: url("/assets/webfonts/Tungsten-ExtraLight.woff2") format("woff2");
    font-style: normal;
}




:root{
    ---clr-primary: #000;
    ---clr-accent: #E2211C;

    ---p-xsmall: 0.25em;
    ---p-small: 0.5em;
    ---p-medium: 1em;
    ---p-large: 1.5em;
    ---p-larger: calc(var(---p-large) + var(---p-small));
    ---p-xlarge: 3em;

    ---m-xsmall: 0.25rem;
    ---m-small: 0.5rem;
    ---m-medium: 1rem;
    ---m-large: 1.5rem;
    ---m-xlarge: 3rem;

    ---tungsten-xl: "Tungsten xtra-light";
    ---tungsten-l: "Tungsten light";
    ---tungsten-m: "Tungsten Medium";
    ---tungsten-sb: "Tungsten Semibold";
    ---tungsten-book: "Tungsten Book";
    ---tungsten-bold: "Tungsten Bold";


    ---player: "i am a player";
}

*, 
*::before, 
*::after{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

h1,
h2,
h3,
h4,
h5 {
    text-transform: uppercase;
    line-height: 1.5;
    color: #fff;
}

li {
    list-style: none;
    display: inline-block;
}

a {
  text-decoration: none;
  display: inline-block;
}

img {
    display: block;
    max-width: 100%;
}

body {
    background-color: var(---clr-primary);
}

.btn-primary {
    white-space: nowrap;
    padding: .75em 2.875em;
    padding: var(---p-medium) var(---p-larger);
    color: #fff;
    font-family: var(---player, "Times New Roman");
    font-weight: 600;
    text-transform: uppercase;
    background-color: var(---clr-accent);
}

.btn-primary:hover{
    color: #000;
    background-color: #fff;
}

/* ==== header section ==== */

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: .25em .75em;
}

.logo--primary{
    flex: 0 auto 120px;
    -ms-flex: 0 auto 120px;
}

.search,
.hamburger-menu {
    cursor: pointer;
    border: none;
    width: calc(16px + 1vw);
    color: #fff;
    background-color: transparent;
}

.nav-mobile {
    flex: 0 1 auto;
    -ms-flex: 0 1 auto;
    margin-left: 4em;
    display: flex;
    
}

.label {
    color: #fff;
    font-size: calc(12px + 1vw);;
    text-transform: uppercase;
    display: flex;
    align-items: center;
    cursor: pointer;
}

.label,
.hamburger-menu {
    margin-left: calc(12px + 1vw);
}


.nav-primary {
    display: none;
}



/* ==== hero section ==== */

.hero__title {
    font-family: var(---tungsten-sb, "Times New Roman");
    font-size: 2rem;
    line-height: 1.2;
    margin-bottom: .5em;
    letter-spacing: 0.025em;
    font-weight: 500;
}

.hero__title:hover{
    text-decoration: underline;
    cursor: pointer;
}

.hero__body {
    padding: 1em;
    border-bottom: 2px solid whitesmoke;
}

/* === upcoming fights === */
.card {
    padding: 1em 1em 2em;
    border-bottom: 2px solid whitesmoke;
}
.card__title--primary{
    text-align: center;
    font-family: var(---player, sans);
    font-weight: lighter;
    font-size: 1.25rem;
    padding: 1em 0;
    position: relative;
}

.red-plus{
    color: var(---clr-accent);
    position: absolute;
    right: 0;
    top: 50%;
    display: inline-block;
    transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
}


.card__body{
    --p-y: 2em;

    padding-top: var(--p-y);
    padding-bottom: var(--p-y);
    margin-bottom: 2em;
    border-top: 2px solid var(---clr-accent);
    border-bottom: 2px solid var(---clr-accent);

    --m-b: .25em;
}

.card__sub-title--accent{
    font-size: 1rem;
    line-height: 1.2;
    color: #979899;
    margin-bottom: var(--m-b);
    font-family: var(---player, sans);
}

.card__sub-title--primary{
    line-height: 1.2;
    font-family: var(---tungsten-sb);
    font-size: 1.75rem;
    margin-bottom: var(--m-b);
}

.card__sub-title--secondary{
    font-size: .75em;
    text-transform: uppercase;
    font-family: var(---player, sans);
    color: #979899;
    margin-bottom: var(--m-b);
    padding-right: 1em;
}

.card__info{
    display: flex;
    justify-content: space-between;
}

.card__info-body{
    align-self: flex-end;
}

.card__info-header{
    color: #979899;
    line-height: 1.2;
    font-family: var(---player, "Times New Roman");
    white-space: nowrap;
    font-size: calc(0.75rem + .25vw);
    margin-right: var(---m-small);
}

.card__info-img{
    flex: 0 1 120px;
    
}


.card > .btn-primary{
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    -webkit-transform: translateX(-50%);
    -moz-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    -o-transform: translateX(-50%);
}

/***** banner ******/
.banner{
    padding: 1.25em 1em;    
}

.banner__img{
    width: 100%;
}

/***** article  ****/

.container{
    background-color: #fff;
    padding: 2em 1em 3em;

    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 12px 16px;
}

.container__title{
    grid-column: span 2;

    color: #000;
    text-align: center;
    font-family: var(---player, "Times New Roman");
    font-weight: 300;
    margin-bottom: 1.5em;
}

.article-main {
    grid-column: span 2;
    padding-bottom: 2.5em;
}

.article-main__thumbnail{
    position: relative;
    margin-bottom: 1em;
}

.article-main__thumbnail > img {
    width: 100%;
}

.article-title-icon{
    display: inline-block;
    margin-right: .75em;
}
.article-main__title--overlay{
    color: #fff;
    background-color: rgba(0, 0, 0, .9);
    padding: .75em .75em;
    text-transform: uppercase;
    font-family: var(---tungsten-bold, sans-serif);
    font-size: calc(.5rem + 2vw);
    width: 100%;
    bottom: 0;
    left: 0;
    position: absolute;
    display: flex;
    align-items: center;

}

.time-length{
color: #979899;

}

.article__header {
    color: #000;
    font-family: var(---tungsten-sb);
    font-size: 2rem;
    
}

.article-main__header--accent{
    background-color: var(---clr-accent);
    color: #fff;
    font-family: var(---tungsten-book);
    font-weight: 600;
    font-size: 1.5rem;
    line-height: 24px;
    padding: 8px 15px;
    /* padding: .75em 1em; */
    text-transform: uppercase;
    display: inline-block;
    margin-bottom: 1em;
}

.article-main__body{
    font-family: sans-serif;
    margin-bottom: 1em;
}


.btn--secondary{
    text-transform: uppercase;
    position: relative;
    font-size: 1.25rem;
    font-family: var(---tungsten-bold);
    color: var(---clr-accent);
}


.article-sub__img{
    font-family: var(---tungsten-m);
    font-weight: 600;
    position: relative;
    margin-bottom: .725em;
}

.article-sub__img::before{
    content: attr(data-content);
    position: absolute;
    left: 0;
    top: 0;
    background-color: rgba(151, 152, 153, .8);
    color: #000;
    padding: 8px 15px;
}

.time-overlay{
    position: absolute;
    left: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 1);
    display: flex;
    align-items: center;
    padding: .25em .75em;
    font-family: inherit;
    /* width: 30px; */
    height: 30px;
}

.article-sub__title{
    font-family: var(---tungsten-bold);
    font-weight: 800;
    text-transform: uppercase;
}


/**** fight alerts  ****/
.cfa{
    padding: 2.5em 1.75em;
    background-color: #979899;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-flow: row wrap;
}


.cfa__title{
    font-size: 3.143em;
    align-self: center;
    line-height: 1.2;
    text-align: center;
    color: #000;
    white-space: nowrap;
    font-family: var(---tungsten-l);
    margin-bottom: .5em;
}

.cfa__body{
    flex: 0 1 100%;
    margin-bottom: .5em;
    font-family: sans-serif;
    line-height: 1.5;
    text-align: center;
}

.cfa__cta{
    white-space: nowrap;
    align-self: flex-start;
    text-transform: uppercase;
    padding: 1em;
    background-color: var(---clr-accent);
    color: #fff;
}

.cfa__cta > span {
    display: flex;
    align-items: center;
    font-family: var(---player, "Times New Roman");
}

.cfa__cta i {
    color: #000;
    margin-right: 1em;
}


/**** boxing news section  */
.media {
    background-color: #fff;
    padding: 2em 1em 3em;
}


.media__header{
    font-family: var(---player);
    color: #000;
    text-align: center;
    margin-bottom: var(---m-medium);
}

.media__container{
    display: flex;
    /* justify-content: flex-start; */
    margin-bottom: calc(var(---m-medium) + 1vw);
}

.article-date {
    display: inline-block;
}

.article-date,
.article-tag{
    font-size: calc(12px + 0.5vw);
    text-transform: uppercase;
    font-family: var(---tungsten-bold);
}

.media__title{
    padding-top: var(---p-xsmall);
    font-family: var(---tungsten-bold);
    color: #000;
    line-height: 1.1;
    font-size: calc(1rem + 1.25vw);
    margin-bottom: var(---m-small);
}

.media__img{
    /* z-index: 5; */
    /* position: relative; */
    margin-right: 14px;
    flex: 0 0.5 200px;
}

.media__img-wrapper{
    position: relative;
}

.media__img-wrapper::before{
    content: '';
    position: absolute;
    display: block;
    left: 0;
    bottom: 0;
    background-image: url(/assets/images/icons/video-icon.png);
    background-repeat: no-repeat;
    background-size: 100%;
    width: 30px;
    height: 30px;
    
}

.media__body{
    flex: 1 1 300px;
}

.article-tag {
    color: var(---clr-accent);
    display: inline-block;
    
}

.media__text{
    display: none;
    font-family: sans-serif;
}

.btn-more{
    margin-top: var(---m-medium);
    border: 2px solid #000;
    font-family: var(---player, "Times New Roman");
    color: #000;
    text-transform: uppercase;
    padding: .75em 1em;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    -webkit-transform: translateX(-50%);
    -moz-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    -o-transform: translateX(-50%);
}





/* social media links */
.social{
    border-top: 2px solid #000;
    background-color: #fff;
    padding: 2em 1em;
}

.social__title{
    color: #000;
    font-family: var(---player, sans-serif);
    text-align: center;
    margin-bottom: 1em;
}

.social-links{
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    -webkit-transform: translateX(-50%);
    -moz-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    -o-transform: translateX(-50%);
}

.social-links span{
    background-color: #000;
    display: inline-block;
    padding: 1em;
}

.social-links i {
    color: #fff;
}

.footer {
    background-color: #000;
    padding: 1em .5em;
}

.footer-nav__links {
    margin-bottom: 1em;
    display: flex;
    flex-wrap: wrap;
}

.footer-nav__links li{
    flex: 0 1 50px;
}

.footer-nav__links li:not(:last-child){
    margin-right: .5em;
}

.footer-nav__links li:nth-child(4){
    flex-basis: max-content;
}
.footer-nav__links li:last-child{
    flex: 0 1 max-content;
}
.footer-nav__links a {
    font-size: 14px;
    color: #fff;
    text-transform: uppercase;
    font-family: var(---player, sans-serif);
}

.footer-social__title{
    font-family: var(---player, sans-serif);
    font-size: 14px;
    margin-bottom: 1em;
}

.footer-social__icons{
    margin-bottom: 2em;
}

.footer-social__icons span{
    display: inline-block;
    margin-left: 2em;
}

.footer-social__icons i{
    color: #fff;
}

.credits p{
    color: #fff;
    text-transform: uppercase;
    font-size: 12px;
    font-family: var(---player, sans-serif);
    padding: 0 2em;
}

@media (min-width: 768px){
    .media__text{
        display: block;
    }

  .cfa__title {
      flex: 0 0 231px;
  }

    .cfa__body {
        flex: 1 0 100px;
    }

    .cfa__cta {
        flex: 0 0 231px;
    }

    .cfa__body,
    .cfa__cta {
        margin-left: calc(1rem + 1vw);
    }


    .footer-social{
        display: flex;
        justify-content: flex-start;
        align-items: center;
        margin-bottom: 1em;
    }

    .footer-social__title,
    .footer-social__icons {
        margin: 0 1em 0 0;
    }
}

@media (min-width: 768px) and (max-width: 991px){    
    .hero {

        border-bottom: 2px solid whitesmoke;
        display: grid;
        grid-template-columns: 62vw 1fr;
        grid-template-rows: repeat(2, 180px);
    }

    .hero__img {
        /* flex: 1 1 500px; */
        position: relative;
        grid-column: span 2;
        grid-row: span 3;
    }

    .hero__img > img {
        object-fit: cover;
        min-height: 100%;
    }

    .hero__body{
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        border: none;
        background-color: rgba(0, 0, 0, .8);
    }

    .card {
        padding: 0 1.25em;
        border: none;
        /* flex: 1 0 300px; */
        grid-row: span 2;
        grid-column: span2;
    }

    .card__body {
        margin-bottom: .75em;
        padding: 1em 0;
    }

    .card__title--primary{
        padding: 0;
    }

    .card__sub-title--primary {
        font-size: 22px;
        line-height: 22px;
    }
    
    
    .card__sub-title--accent {
        font-size: 14px;
        line-height: 14px;
    }

    /* .card__info-header {
        font-size: 2vw;
    } */

    .red-plus{
        display: none;
    }

} 